"use client"
import Image from "next/image"


const FriendRequestList = ({requests}:{requests:Request})=>{
    return(
        <div className="">
        {<div className="flex items-center justify-between">
            <div className=" flex items-center gap-4">
                <Image src="/r1.jpg" alt="" width={40} height={40} className="w-10 h-10 rounded-full object-cover"/>
                <span className="font-semibold"> Miyuri Lokuhewage</span>
            </div>
            <div className="flex gap-3 justify-end">
            <Image src="/accept.png" alt="" width={20} height={20} className="cursor-pointer"/>
            <Image src="/reject.png" alt="" width={20} height={20} className="cursor-pointer"/>
            </div>
            
        </div>}

        </div>
    )
}

export default FriendRequestList